<template>
	<v-tab-item>
		<v-row class="mt-3">
			<v-col 
				cols="12" sm="4"
				v-for="(item, i) in 3" :key="i">
				<v-img src="@/assets/se.png" max-width="100%" max-height="150"></v-img>
				<div class="title black--text">Smartedu</div>
        <div class="subtitle-1 grey--text">Smartedu</div>
			</v-col>
		</v-row>
		<v-dialog
      v-model="dialog"
      width="500"
      persistent>
      <template v-slot:activator="{ on }">
				<v-btn
					v-on="on"
					outlined
					color="primary">
					<v-icon color="primary">mdi-plus-circle</v-icon>
					add tenant
				</v-btn>
      </template>

      <v-card>
        <div class="pa-2 text-center">
          <span class="title">Added Tenant</span>
        </div>
        <v-card-text>
          <v-form v-model="valid" ref="form">
            <v-text-field
              v-model="dataTenant.name"
              label="Name"
              dense
              :rules="requiredRule"
              autocomplete="off"
            ></v-text-field>  
            <v-text-field
              v-model="dataTenant.category"
              label="Category"
              dense
              :rules="requiredRule"
              autocomplete="off"
            ></v-text-field>   
            <v-file-input
              accept="image/*"
              show-size
              counter
              :rules="requiredRule"
              label="Logo"
            ></v-file-input>         
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
	</v-tab-item>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataTenant: {
          name: '',
          category: ''
        },
        requiredRule: [v => !!v || 'Data harus diisi']
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          // console.log(this.detailKelas)
        }
      }
    }
  }
</script>